.Feed {
  flex: 1;
  margin: 0 0 30px;
  text-align: center; }

.Feed-content {
  position: relative;
  padding: 20px 0;
  text-align: left; }

.FeedItem {
  display: flex;
  position: relative;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 0 20px; }

.FeedItem:last-child {
  margin: 0; }

.FeedItem-avatar {
  position: absolute;
  z-index: 10;
  flex: 1;
  width: 60px;
  height: 60px; }
  .FeedItem-avatar::after {
    content: '';
    position: absolute;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    border-radius: 6px;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.5); }

@media only screen and (max-width: 544px) {
  .Feed-content .FeedItem {
    margin-bottom: 40px; }
  .FeedItem .FeedItem-avatar {
    margin: 20px;
    border-radius: 8px; }
    .FeedItem .FeedItem-avatar::after {
      box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2); } }

@media only screen and (min-width: 545px) and (max-width: 899px) {
  .Feed-content .FeedItem {
    justify-content: center;
    margin-bottom: 40px; }
  .FeedItem .FeedItem-avatar {
    margin: 20px;
    border-radius: 8px;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2); }
    .FeedItem .FeedItem-avatar::after {
      box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2); } }
